ul {
    padding: 0;
    margin: 0;
    list-style: none;
}

a {
    text-decoration: none;
    outline: none;
}

p {
    margin: 0;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

h1,
h2,
h3 {
    margin: 0;
}

input {
    margin: 0;
    outline: none;
}

button {
    outline: none;
}